body {
  text-transform: uppercase;
  --header-height: 60px;
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav {
  text-align: center;
  align-items: center;
  margin-right: 50px;
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  height: var(--header-height);
  font-size: 12px;
  line-height: var(--header-height);
  font-weight: 500;
  text-decoration: none;
}

.nav__item {
  margin: 0 20px;
  display: block;
}

.nav__item:hover {
  color: var(--blue-color);
  text-decoration: underline;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.blue_link {
  color: var(--blue-color);
}

.image_logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

